.fade-enter {
  opacity: 0;
  z-index: 1;
}
.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 250ms ease-in;
}
.fade-appear, .fade-enter {
  opacity: 0;
}
.fade-appear-active, .fade-enter-active {
  transition: opacity 300ms linear;
  opacity: 1;
}
.fade-exit {
  transition: opacity 200ms linear;
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
}
