.slide {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #777;
    transition: .4s;
    padding: 0px;
    color: #fff;
    border-radius: 2em;
} .slide:before {
    position: absolute;
    content: "";
    height: 1.5em;
    width: 1.5em;
    right: 2.25em;
    top:50%;
    transform: translateY(-50%);

    background-color: #EDF4FC;
    transition: .4s;
    border-radius: 2em;
}
input:checked + .slide {
    background-color: #2A40BE;
}
input:checked + .slide:before {
    transform: translateX(26px), translateY(-50%);
    right: 0.25em;
}

